import React, { useContext } from "react";
import s from "./index.module.css";
import cn from "classnames";
import { calcDiscountPrice, isLiked, createMarkup} from "../../utils/product";
import {ReactComponent as Save} from "./img/save.svg";
import truck from './img/truck.svg';
import quality from './img/quality.svg';
import { useNavigate } from "react-router-dom";
import { UserContext } from "../../Context/newContext";

export const Product = ({ onProductLike, pictures, likes=[], revievs, tags, name,price,discount,description, weight, _id}) => {
  const{user:currentUser, handleLike} = useContext(UserContext);
   
  const navigate = useNavigate();
  const discount__price = calcDiscountPrice(price, discount);
  const isLike = isLiked(likes, currentUser?._id)
  const descriptionHTML = createMarkup(description);

  return (
  <>
    <div>
      <a href="#" className="button-back" onClick={()=>navigate(-1)}></a>
      <h1 className={s.productTitle}>{name}</h1>
      <div>
        <span></span>: <b>874158</b>
      </div>
    </div>
    <div className={s.product}>
      <div className={s.imgWrapper}>
        <img src={pictures} alt={` ${name}`} />
      </div>
      <div className={s.desc}>
        <span className={discount ? s.oldPrice : s.Price}>{price}&nbsp;.</span>

        {discount && <span className={cn(s.price, 'card__price_type_discount')}>{discount__price}&nbsp;.</span>}

        <div className={s.btnWrap}>
          <div className={s.left}>
            <button className={s.minus}>-</button>
            <span className={s.num}>0</span>
            <button className={s.plus}>+</button>
          </div>
          <a href="#" className={(cn("btn", "btn_type_primary", s.cart))}> </a>
        </div>
        <button className={cn(s.like, {[s.likeActive]: isLike})}  onClick={onProductLike}>
          <Save/>
        <span>{isLike ? ' ' : ' '}</span>
        </button>

        <div className={s.delivery}>
          <img src={truck} alt="truck" />
          <div className={s.right}>
            <h3 className={s.name}>   !</h3>
            <p className={s.text}>  <span className={s.bold}> 399 </span></p>
          </div>
        </div>

        <div className={s.delivery}>
          <img src={quality} alt="quality" />
          <div className={s.right}>
            <h3 className={s.name}>   !</h3>
            <p className={s.text}>  <span className={s.bold}> 399 </span></p>
          </div>
        </div>
      </div>
    </div>

    <div className={s.box}>
      <h2 className={s.title}></h2>
      {/* 	<p className={s.subtitle} dangerouslySetInnerHTML={createMarkupDescription()}>
				</p> */}
      <h2 className={s.title}></h2>
    	<div className={s.grid}>
					<div className={s.naming}></div>
					<div className={s.description}>1  120-200 </div>
					<div className={s.naming}></div>
					<div className={s.description}>490   100 </div>
					<div className={s.naming}></div>
					<div className={s.description}>
						<p>
							     
							      .
						</p>
						<p>     .</p>
						<p>
							  ,     
							.
						</p>
						<p>
							   ,   
							  ,     .
						</p>
						<p>    .</p>
					</div>
				</div>
			</div>


  </>

  )
}